



























import Tabs from '@/components/Tabs.vue';
import Vue from 'vue';
import {Component, Watch} from 'vue-property-decorator';
import typeList from '@/contants/typeList';
import showTypeList from '@/contants/showTypeList';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import Chart from '@/components/Chart.vue';
import _ from 'lodash';

@Component({
  components: {Chart, Tabs}
})
export default class Statistics extends Vue {
  @Watch('mounted')
  tagString(tags: Tag[]) {
    return tags.length === 0 ? '无' : tags.map(t => t.name).join(', ');
  }

  get recordList() {
    return (this.$store.state as RootState).recordList;
  }

  get groupedList() {
    const {recordList} = this;
    type Result = {
      title: string; total?: number; items: RecordItem[];
    }[];
    const newList = clone(recordList).filter(r => r.type === this.type).sort((a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());
    console.log(newList);
    if (newList.length === 0) {return [];}
    const result: Result = [{title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'), items: [newList[0]]}];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), 'day')) {
        last.items.push(current);
      } else {
        result.push({title: dayjs(current.createdAt).format('YYYY-MM-DD'), items: [current]});
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
    });
    return result;
  }

  timeTitle(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(now, 'day')) {
      return '今天';
    } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
      return '昨天';
    } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
      return '前天';
    } else if (day.isSame(now, 'year')) {
      return day.format('MM月D日');
    } else {
      return day.format('YYYY年MM月D日');
    }
  }

  get option() {
    const today = new Date();
    const array = [];
    for (let i = 0; i <= 29; i++) {
      const key = dayjs(today).subtract(i, 'day').format('YYYY-MM-DD');
      const found = _.find(this.groupedList, {title: key});
      array.push({key: key, value: found && found.total || 0});
    }
    array.reverse();
    const keys = array.map(item => item.key);
    const values = array.map(item => item.value);
    return {
      tooltip: {
        show: true,
        triggerOn: 'click',
        formatter: '{c}',
        position: 'top'
      },
      grid: {
        left: 0,
        right: 0
      },
      xAxis: {
        axisTick: {alignWithLabel: true},
        type: 'category',
        data: keys,
        axisLabel: {
          formatter: function (value: string) {
            return value.substr(5);
          }
        }
      },
      yAxis: {
        type: 'value',
        show: false
      },
      series: [{
        itemStyle: {color: 'rgb(255, 153, 0)'},
        symbolSize: 8,
        data: values,
        type: 'line'
      }]
    };
  }

  beforeCreate() {
    this.$store.commit('fetchRecord');
  }

  mounted() {
    const chartWidth = (this.$refs.chartWrapper as HTMLDivElement);
    chartWidth.scrollLeft = chartWidth.scrollWidth;
  }

  type = '-';
  typeList = typeList;
  listType = 'imageList';
  showTypeList = showTypeList;
}
